import React, { useEffect, useState } from 'react';
import './App.css'
import Card from './Card';


const App=()=>{

  const APP_ID='8efcd38a';
  const APP_KEY='d688ca1a3dc8a896db9bba6e6a8a393b';
  
  const[recipes,setRecipes]=useState([]);
  const[search,setSearch]=useState('');
  const[query,setQuery]=useState('pasta');

  

  useEffect(()=>{
    const getRecipes=async()=>{
      const response= await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`
      );
      const data= await response.json();
      setRecipes(data.hits);
   
    };
    getRecipes();  
   }, [query]);

  
  
  const updateSearch=(e)=>{
     setSearch(e.target.value);  
  };

  const getSearch=e=>{
    e.preventDefault();
    setQuery(search);
  }

 

  return(   
  
      <div className='App'> 

       <div className='container-fluid nav-bg text-center my-4'>
        <div className='row'>
        <div className='col-10 mx-auto'>
       <h1 className='header'>Food Recipes</h1>  
        </div>
        </div>
        </div>
      
      <form className='search-form text-center my-5' onSubmit={getSearch}>
        <input type='text' className='status-bar' value={search} onChange={updateSearch}/>
        <button className='search-button' type='submit' >Search</button>
      </form>

     
      <div className='container-fluid nav-bg'>
        <div className='row'>
        <div className='col-10 mx-auto'>
        <div className='gy-5'>

      {recipes.map((recipe,ind)=>(
        <Card
        key={ind}
        title={recipe.recipe.label} 
        calories={recipe.recipe.calories}
        image={recipe.recipe.image}
        ingredients={recipe.recipe.ingredients}
         />  
      ))};
     

     
        
      </div>
      </div>
        </div>
        </div>
       </div>

  );

}


export default App;
